import React, { Component } from 'react'
import "./Navbar.css"

export default class Navbar extends Component {
  render() {
    return (
        <div className="container">
            <div className="navbar">

            <img src="/Images/Digital Marketing.svg" alt="" />
            <div className="nav-menu">
                <ul>
                    <li>
                        <a href="#">Портфолио</a>
                    </li>
                    <li>
                        <a href="#">Услуги и Тарифы</a>
                    </li>
                    <li>
                        <a href="#">О Компании</a>
                    </li>
                    <li>
                        <a href="#">FAQ</a>
                    </li>
                </ul>
            </div>

            <select class="form-select lang-select">
                <img src="public/Images/Uzb.svg" alt="" />
              <option>Рус</option>
              <option>Eng</option>
              <option>O'zb</option>
            </select>

                <button id='mybtn'>Оставить заявку</button>
            </div>
        </div>
    )
  }
}
