import React, { Component } from 'react'
import BodyList from './BodyList'
import "./Body.css"

export default class Body extends Component {
  state = {
    body: [
      {
      img: '/Images/Sumka.svg',
      ball: "5+",
      text: "Лет опыта"
    },
    {
      img: '/Images/Флаг.svg',
      ball: "80+",
      text: "Проектов в СММ / мес"
    },
    {
      img: '/Images/Group.svg',
      ball: "20+",
      text: "Специалистов сферы"
    },
    {
      img: '/Images/Light.svg',
      ball: "999+",
      text: "Тонн креатива"
    }
  ],
    portfolioCard: [
      {
        img: "./Images/Latest.png",
        textToRead: "8 min to read",
        title: "15 Latest Development in Webflow."
      },
      {
        img: "./Images/Create.png",
        textToRead: "12 min to read",
        title: "Create your wedding website"
      },
      {
        img: "./Images/Schedule.png",
        textToRead: "13 min to read",
        title: "Schedule ceremony site tours"
      },
      {
        img: "./Images/Write.png",
        textToRead: "9 min to read",
        title: "Write a sweet note to your partner"
      }
    ]
  
}
  render() {
    const {body, portfolioCard} = this.state;
    return (
        <div className="body">
          <div className='container'>

        <div className="body-title">
            Портфолио
        <div>
            Ознакомьтесь с нашим портфолио, где каждый проект — это история успеха. 
            Мы гордимся достижениями наших клиентов и готовы воплотить в жизнь вашу уникальную идею.
        </div>

        </div>

        <div className="row info-row">
                {
                  body.map((item) => (          
                      <div className="col-xl-3">
                        <BodyList item={item}/>
                      </div>
                  ))
                }
            </div>

        <div className="row">
          {
            portfolioCard.map((item)=> (
              <div className="col-xl-3">
                <div className="card rounded-5">
                    <img className='card-img-top' src={item.img} alt="" />
                  <div className="card-body my-card">
                    <span>{item.textToRead}</span>
                    <div className="title">{item.title}</div>
                  </div>
                </div>
              </div>
            ))
          }
        </div>
        </div>
      </div>
    )
  }
}