import React, { Component } from 'react'
import "./AccordionSection.css"

export default class AccordionSection extends Component {
  render() {
    return (
      <div className='container accordion'>
        <div className="accordion-title">
        Часто задаваемые вопросы (FAQ)
        </div>

            <div id="accordion">
        <div className="row">
            <div className="col-xl-6">

<div class="card">
  <div class="card-header">
    <a class="btn" data-bs-toggle="collapse" href="#collapseOne">
    Какие результаты я могу ожидать от ваших услуг?
    <img src="/images/arrow-down-s-line.svg" alt="" />
    </a>
  </div>
  <div id="collapseOne" class="collapse show" data-bs-parent="#accordion">
    <div class="card-body">
    Результаты от услуг зависят от ваших
    целей и выбранного тарифного плана, 
    но обычно это повышение узнаваемости бренда,
    увеличение трафика на сайт и улучшение вовлеченности аудитории.
    </div>
  </div>
</div>

<div class="card">
  <div class="card-header">
    <a class="collapsed btn" data-bs-toggle="collapse" href="#collapseTwo">
        Каковы стоимость и структура вашего тарифного плана?
        <img src="/images/arrow-down-s-line.svg" alt="" />
    </a>
  </div>
  <div id="collapseTwo" class="collapse" data-bs-parent="#accordion">
    <div class="card-body">
    Цена и структура тарифного плана обычно 
    представлены в виде пакетов услуг с разным уровнем обслуживания и ценой.
    </div>
  </div>
</div>

<div class="card">
  <div class="card-header">
    <a class="collapsed btn" data-bs-toggle="collapse" href="#collapseThree">
    Предоставляете ли вы отчетность о проделанной работе и её эффективности?
    <img src="/images/arrow-down-s-line.svg" alt="" />
    </a>
  </div>
  <div id="collapseThree" class="collapse" data-bs-parent="#accordion">
    <div class="card-body">
    Да, мы предоставляем регулярные отчеты о 
    проделанной работе, анализ эффективности стратегий 
    и достигнутых результатов.
    </div>
  </div>
</div>

</div>

            <div className="col-xl-6">

<div class="card">
  <div class="card-header">
    <a class="btn" data-bs-toggle="collapse" href="#collapseFour">
    Сколько времени займет достижение желаемых результатов?
    <img src="/images/arrow-down-s-line.svg" alt="" />
    </a>
  </div>
  <div id="collapseFour" class="collapse" data-bs-parent="#accordion">
    <div class="card-body">
    Время получения результатов может варьироваться 
    от нескольких недель до нескольких месяцев в 
    зависимости от сложности задачи и конкуренции 
    в вашей отрасли.
    </div>
  </div>
</div>

<div class="card">
  <div class="card-header">
    <a class="collapsed btn" data-bs-toggle="collapse" href="#collapseFive">
    Предоставляете ли вы гарантии качества выполненной работы?
    <img src="/images/arrow-down-s-line.svg" alt="" />
    </a>
  </div>
  <div id="collapseFive" class="collapse" data-bs-parent="#accordion">
    <div class="card-body">
    Да, мы гарантируем качество нашей работы и 
    готовы вносить коррективы по мере необходимости 
    для достижения наилучших результатов.
    </div>
  </div>
</div>

<div class="card">
  <div class="card-header">
    <a class="collapsed btn" data-bs-toggle="collapse" href="#collapseSix">
    Есть ли у вас какие-либо знания или опыт в моей области?
    <img src="/images/arrow-down-s-line.svg" alt="" />
    </a>
  </div>
  <div id="collapseSix" class="collapse" data-bs-parent="#accordion">
    <div class="card-body">
    Мы имеем опыт работы в различных отраслях и 
    готовы адаптировать наши стратегии к конкретным 
    потребностям вашего бизнеса.
    </div>
  </div>
</div>

</div>
            </div>
        </div>
        </div>
    )
  }
}
