import React, { Component } from 'react'
import "./FormElement.css"

export default class FormElement extends Component {
  render() {
    return (
      <div className='myGradient'>
        <div className="container">
            <div className="row form-content">
                <div className="col-xl-6">
                    <div className="title-fm">
                    Начните новый этап развития с нами
                    </div>
                    <div className="text-fm">
                    Remember, when you invest your capital is at risk.
                    </div>
                </div>

                <div className="col-xl-6 form-element">
                    <div className="row form-top">
                        <div className="col-xl-6">
                            <form>
                                <input type="text" placeholder='Имя' id='firstName' />
                                <label htmlFor="firstName"></label>
                            </form>
                        </div>
                        <div className="col-xl-6">
                            <form>
                                <input type="text" placeholder='Номер телефона' id='phoneNumber' />
                                <label htmlFor="phoneNumber"></label>
                            </form>
                        </div>
                    </div>
                    <div className="row form-bottom">
                        <div className="col-xl-6">
                            <form>
                                <input type="text" placeholder='Название компании' id='company' />
                                <label htmlFor="company"></label>
                            </form>
                        </div>
                        <div className="col-xl-6">
                            <button type='submit' id='btn-submit'>Отправить</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
      </div>
    )
  }
}
