import React, { Component } from 'react'

export default class BodyList extends Component {
  render() {
    const {item} = this.props
    return (
        <div className="body-info">
                  <div className="d-flex">
                      <img src={item?.img} alt="" />
                      <div className="ball">{item?.ball}</div>
                    </div>
                  <div className="text">
                      {item?.text}
                    </div>
                </div>
    )
  }
}
