import React, { Component } from 'react'
import "./SectionTwo.css"

export default class SectionTwo extends Component {
  render() {
    return (
      <div className='SectionTwo'>
        <div className="container">
            <div className="row">
                <div className="col-xl-5">
                    <img src="./Images/Group section Two.svg" alt="" />
                </div>

                <div className="col-xl-7 right-page">
                    <div className="title">
                    3 преимущество работы с нами
                    </div>

                    <div className="step-one d-flex">
                        <div>
                            <img src="./images/Inovation.svg" alt="" />
                        </div>

                        <div className="step-title">
                        Инновации в каждом проекте

                            <div className="step-text">
                                    "Мы предоставляем передовые технологии и инновационные решения, чтобы ваш проект всегда выделялся."
                                </div>
                            </div>
                        </div>

                        <div className="step-two">
                            <img src="./images/Step 2.svg" alt="" />
                            <span>Step 2</span>
                        </div>

                        <div className="step-two">
                            <img src="./images/Step 3.svg" alt="" />
                            <span>Step 3</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
  }
}
