import React, { Component, useLayoutEffect } from 'react'
import "./Header.css"
import gsap from 'gsap'




export default class Header extends Component {
    render() {
      return (
      <div className='container header'>
            <div className="row">
                <div className="col-xxl-6 header-left">
                    <div className="logo">
                        Digital Marketing
                    </div>

                    <div className="header-title">
                    Приведите свой бизнес к новым высотам с нашей компанией!
                    <div>Надежные решения для вашего успеха.</div>
                    </div>

                    <button className='header-btn'>Бесплатная Консультация</button>
                </div>

                <div className="col-xxl-4">
                    <div className='header-img'>
                        <img className='img-fluid' src="/Images/Header.png" alt="" />
                    </div>
                </div>
            </div>
        </div>  
    )
  }
}
