import React, { Component } from 'react'
import Slider from 'react-slick';
import "./CarouselSection.css"
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

export default class CarouselSection extends Component {
    state = {
        carousel:[
            {
            img: "./Images/Dot.svg",
            title: "Great movie podcasts add to the conversation around a film and avoid mere self-indulgence - here’s a breakdown of the best.",
            user: "./Images/Darrel.svg",
            fullName: "Darrell Steward",
            text: "Happy User"
        },
        {
            img: "./Images/Dot.svg",
            title: "For athletes, high altitude produces two contradictory effects on performance. For explosive events ",
            user: "./Images/Marvin.svg",
            fullName: "Marvin McKinney",
            text: "Happy User"
        },
        {
            img: "./Images/Dot.svg",
            title: "Physiological respiration involves the mechanisms that ensure that the composition of the functional",
            user: "./Images/Cameron.svg",
            fullName: "Cameron Williamson",
            text: "Happy User"
        },
        {
            img: "./Images/Dot.svg",
            title: "Great movie podcasts add to the conversation around a film and avoid mere self-indulgence - here’s a breakdown of the best.",
            user: "./Images/Darrel.svg",
            fullName: "Darrell Steward",
            text: "Happy User"
        },
        {
            img: "./Images/Dot.svg",
            title: "For athletes, high altitude produces two contradictory effects on performance. For explosive events ",
            user: "./Images/Marvin.svg",
            fullName: "Marvin McKinney",
            text: "Happy User"
        },
        {
            img: "./Images/Dot.svg",
            title: "Physiological respiration involves the mechanisms that ensure that the composition of the functional",
            user: "./Images/Cameron.svg",
            fullName: "Cameron Williamson",
            text: "Happy User"
        }
    ]
    }
  render() {
    const {carousel} = this.state

    const settings = {
        dots: false ,
        infinite: true,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 1
        
      };
    return (
        <div className='carouselSection'>
            <div className="container">
                <div className="title">
                </div>

                <div className="carousel">
                <div>
        <h2>Что говорят о нас наши клиенты ?</h2>
        <Slider {...settings}>
        {carousel.map((item) => (
            <div className='row carousel-box'>
                <div className="col-xl-4">
                    <div className="box">
                        <div className="carousel-img">
                            <img src={item.img} alt="" />
                        </div>
                        <div className="carousel-title">
                            {item.title}
                        </div>
                        <div className="carousel-boxItem">
                            <img src={item.user} alt="" />
                            <div className="user-box">
                                    <h6 className="carousel-name">{item.fullName}</h6>
                                    <div className="carousel-text">{item.text}</div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        ))}
        </Slider>
      </div></div>                
            </div>
      </div>
    )
  }
}
